import { Routes, Route } from "react-router-dom";
import { useState } from "react";
import Home from "./pages/Home";
import NotFound from "./pages/NotFound";
import MyFavorite from "./pages/MyFavorite";
import BusStatusPage from "./pages/BusStatusPage";
import NearByBusStop from "./pages/NearByBusStop";


function App() {
  const [routeNumber, setRouteNumber] = useState(""); // 
  const [routeName, setRouteName] = useState("");
  const [city, setCity] = useState(''); // choose city
  const [stopData, setStopData] = useState([]); // 
  const [favorites, setFavorites] = useState([]);

  return (
    <div className="App">
      <Routes>
        <Route path="/"
          element={<Home
            routeNumber={routeNumber}
            setRouteNumber={setRouteNumber}
            routeName={routeName}
            setRouteName={setRouteName}
            setCity={setCity}
            city={city} />}>
        </Route>

        <Route path="/myfavorite"
          element={<MyFavorite
            favorites={favorites}
            setFavorites={setFavorites} />}></Route>

        <Route path="/:cityselect/:routeName"
          element={<BusStatusPage
            routeNumber={routeNumber}
            setRouteNumber={setRouteNumber}
            city={city}
            setCity={setCity}
            stopData={stopData}
            setStopData={setStopData}
            favorites={favorites}
            setFavorites={setFavorites} />} >
        </Route>

        <Route path="nearbystop"
          element={<NearByBusStop
            city={city}
            routeName={routeName} />}>
        </Route>

        <Route path="*"
          element={<NotFound />}></Route>
      </Routes>
    </div>
  );
}

export default App;
